import { Store } from "../core/common";
import Search from "../components/Search";

// ----- Movie-app Ïùò ÏòÅÌôî Ï†ïÎ≥¥ ------ //
const store = new Store({
  searchText: "",
  page: 1,
  pageMax: 1, // ÏòÅÌôî ÎçîÎ≥¥Í∏∞ ÏµúÎåÄ ÌéòÏù¥ÏßÄ
  movies: [], // Í≤ÄÏÉâÏñ¥Î•º Ìè¨Ìï®Ìïú Ï†ÑÏ≤¥ ÏòÅÌôî Ï†ïÎ≥¥
  movie: {}, // ÏÑ†ÌÉù ÏòÅÌôî ÏÉÅÏÑ∏ Ï†ïÎ≥¥
  loading: false,
  message: "Search for the movie title!",
});

export default store;

/* ----- storeÏùò ÏÉÅÌÉúÎ•º Î≥ÄÍ≤ΩÌïòÎäî Ìï®Ïàò ----- */
export const searchMovies = async (page) => {
  store.state.loading = true;
  // MovieListMore Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï†ÑÎã¨Ìïú page Ï†ïÎ≥¥Î•º ÏóÖÎç∞Ïù¥Ìä∏ Ìï®
  store.state.page = page;
  // ÏÉàÎ°úÏö¥ ÏòÅÌôîÎ•º Í≤ÄÏÉâÌïúÎã§Î©¥, ÌéòÏù¥ÏßÄÎäî 1Ïùº Í≤ÉÏù¥Í≥†, ÏòÅÌôî Ï†ïÎ≥¥Îäî Ï¥àÍ∏∞ÌôîÎêòÏñ¥Ïïº ÌïúÎã§.
  if (page === 1) {
    store.state.movies = [];
    store.state.message = "";
  }
  try {
    const res = await fetch(
      `https://omdbapi.com?apikey=7035c60c&s=${store.state.searchText}&page=${page}`
    );

    const { Search, totalResults, Response, Error } = await res.json();
    if (Response === "True") {
      store.state.movies = [...store.state.movies, ...Search];

      store.state.pageMax = Math.ceil(Number(totalResults) / 10);
    } else {
      store.state.message = Error;
    }
  } catch (error) {
    console.error("searchMovies error: ", error);
  } finally {
    store.state.loading = false;
  }
  //
};

export const getMovieDetails = async (id) => {
  try {
    const res = await fetch(
      `https://omdbapi.com?apikey=7035c60c&i=${id}&plot=full`
    );
    store.state.movie = await res.json();
  } catch (error) {
    console.log("getMovieDetails Error: ", error);
  }
};

